@tailwind base;
@tailwind components;
@tailwind utilities;




:root {
    --foreground-rgb: rgb(211, 216, 232);
}

@media (prefers-color-scheme: dark) {
    :root {
        --foreground-rgb: 255, 255, 255;
    }
}

body {
    color: rgb(var(--foreground-rgb));
    background-color: #0d1224;
}
